<template>
  <h1>
    <ContactForm :extraHeader="false">

      <!--  HEADER CONTENT  -->
      <div style="width:100%" slot="header-content">
        <v-btn @click="backBtn" class="theme-color back-btn" icon outlined>
          <v-icon class="theme-color">mdi-chevron-left</v-icon>
        </v-btn>
        <v-row justify="center" align="center">
          <v-col class="text-center title" cols="12">
            {{ startConversationText }}
          </v-col>
          <v-col style="margin-top:-10px" class="text-center body-2" cols="12">
            {{ startConversationSubText }}
          </v-col>
        </v-row>
      </div>

      <!--  BODY CONTENT  -->
      <div slot="body-content">
        <v-row align="center" justify="center">
          <v-col cols="6">
            <v-text-field
              class="white text-field"
              v-model="setSubject"
              :color="outlineColor"
              outlined
              :label="subject"
              :rules="rules"
              hide-details="auto"
            ></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-select
              class="white"
              v-model="setCategory"
              :color="outlineColor"
              :items="getCategories"
              outlined
              :label="category"
          ></v-select>
          </v-col>
          <v-col cols="6">
            <v-text-field
              class="white text-field"
              v-model="setFullName"
              :color="outlineColor"
              outlined
              :label="fullName"
              :rules="rules"
              hide-details="auto"
              :value="getFullname"
            ></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-text-field
              class="white text-field"
              v-model="setEmail"
              :color="outlineColor"
              outlined
              :label="yourEmail"
              :rules="rules"
              hide-details="auto"
              :value="getEmail"
            ></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-text-field
              class="white text-field"
              v-model="setTxId"
              :color="outlineColor"
              outlined
              :label="transactionId"
              hide-details="auto"
            ></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-text-field
              class="white text-field"
              v-model="setIssueOccuredDate"
              :color="outlineColor"
              outlined
              :label="timeAndDateWhenOccured"
              :placeholder="getTodayDate"
              hide-details="auto"
            ></v-text-field>
          </v-col>
          <v-col cols="12">
          <v-textarea
              class="white"
              v-model="setMessage"
              :color="outlineColor"
              outlined
              :label="message"
              :rules="rules"
              hide-details="auto"
            ></v-textarea>
          </v-col>
        </v-row>

        <v-row class="px-4 pt-1" align="center">
          <v-btn :loading="sendingMsg" @click="sendMessage" :disabled="!sendBtnEnabled" depressed block class="theme-bg">
            {{ sendMsgText }}
          </v-btn>
        </v-row>

      </div>
    </ContactForm>
  </h1>
</template>

<script>
import ContactForm from '@/components/ContactForm'
import { mapActions, mapState } from 'vuex'
import { sendMessageHelper } from '@/utils/helpers'
import { statusTypes } from '@/utils/types'

export default {
  name: 'Contact-page',
  components: {
    ContactForm
  },
  data: () => ({
    rules: [
      value => !!value || 'Required.',
      value => (value && value.length >= 3) || 'Min 3 characters'
    ],
    choosedCategory: ''
  }),
  computed: {
    ...mapState({
      sendingMsg: ({ appState }) => appState.sendingMsg,
      config: ({ appState }) => appState.config,
      formData: ({ userData }) => userData.formData,
      textKeys: ({ appState }) => appState.textKeys
    }),
    sendBtnEnabled () {
      const { formData } = this
      return formData.fullName.length > 3 && formData.email.length > 3 && formData.subject.length > 3 && formData.message.length > 3
    },
    setFullName: {
      get: function () {
        return this.formData.fullName
      },
      set: function (val) {
        this.setFormData({ fullName: val })
      }
    },
    setSubject: {
      get: function () {
        return this.formData.subject
      },
      set: function (val) {
        this.setFormData({ subject: val })
      }
    },
    setCategory: {
      get: function () {
        return this.formData.category
      },
      set: function (val) {
        this.setFormData({ category: val })
      }
    },
    setEmail: {
      get: function () {
        return this.formData.email
      },
      set: function (val) {
        this.setFormData({ email: val })
      }
    },
    setTxId: {
      get: function () {
        return this.formData.transactionId
      },
      set: function (val) {
        this.setFormData({ transactionId: val })
      }
    },
    setIssueOccuredDate: {
      get: function () {
        return this.formData.issueOccuredDate
      },
      set: function (val) {
        this.setFormData({ issueOccuredDate: val })
      }
    },
    setMessage: {
      get: function () {
        return this.formData.message
      },
      set: function (val) {
        this.setFormData({ message: val })
      }
    },
    getFullname () {
      return (this.config.additionalTicketData && this.config.additionalTicketData.fullName) || ''
    },
    getEmail () {
      return (this.config.additionalTicketData && this.config.additionalTicketData.email) || ''
    },
    getCategories () {
      return this.config.allCategories || ['{allCategories as array}']
    },
    outlineColor () {
      return getComputedStyle(document.body).getPropertyValue('--theme-bg')
    },
    startConversationText () {
      return this.textKeys.startConversationText || '{startConversationText}'
    },
    startConversationSubText () {
      return this.textKeys.startConversationSubText || '{startConversationSubText}'
    },
    fullName () {
      return (this.textKeys.fullName || '{fullName}') + ' *'
    },
    yourEmail () {
      return (this.textKeys.yourEmail || '{yourEmail}') + ' *'
    },
    subject () {
      return (this.textKeys.subject || '{subject}') + ' *'
    },
    message () {
      return (this.textKeys.message || '{message}') + ' *'
    },
    category () {
      return (this.textKeys.category || '{category}')
    },
    transactionId () {
      return (this.textKeys.transactionId || '{transactionId}')
    },
    timeAndDateWhenOccured () {
      return (this.textKeys.timeAndDateWhenOccured || '{timeAndDateWhenOccured}')
    },
    getTodayDate () {
      return new Date().toISOString().replace('T', ' ').substr(0, 16)
    },
    sendMsgText () {
      return this.textKeys.sendMsgText || '{sendMsgText}'
    }
  },
  methods: {
    ...mapActions([
      'setFormData',
      'setSendingMsg',
      'setTicketStatus'
    ]),
    backBtn () {
      this.$router.push({ path: '/', query: this.$route.query })
        .catch(error => {
          console.info(error.message)
        })
    },
    successCallback () {
      this.setTicketStatus(statusTypes.SUCCESS)
    },
    failCallback () {
      this.setTicketStatus(statusTypes.FAILED)
    },
    async sendMessage () {
      this.setSendingMsg(true)
      try {
        const res = await sendMessageHelper(this.formData)
        console.log(res)
        if (res.status === 200) {
          this.successCallback()
        } else {
          this.failCallback()
        }
      } catch (err) {
        this.failCallback()
      }
      this.setSendingMsg(false)
      this.$router.push({ path: '/status', query: this.$route.query })
    }
  }
}
</script>

<style lang="scss" scoped>
.back-btn {
  position: absolute;
}
</style>
